/* CSS Styling for index.js */

body {
  margin: 0;
  font-family: 'Roboto' !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(231,130,36);
  background: linear-gradient(90deg, rgba(231,130,36,1) 0%, rgba(226,87,0,1) 100%);
}

.site-container {
  min-height: 100vh;
}

.header {
  margin-top: 2rem;
  color: white;
  border-radius: 25px;
  text-align: center;
}

.lds-ring {
  display: block;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  width: 80px;
  height: 80px;
}

.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #ffffff;
  border-radius: 50%;
  -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #ffffff transparent transparent transparent;
}

.lds-ring div:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s;
}

.lds-ring div:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}

.lds-ring div:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s;
}

@-webkit-keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
/* CSS Styling for App.js */
.App {
  text-align: center;
}

.header {
  padding: 0.5rem;
  font-size: 2rem;
  color: white;
  text-align: center;
  font-weight: 700;
}

.footer {
  text-align: center;
  color: white;
  padding: 0.5rem;
}



/* CSS Styling for AboutBox.js */

.article-link{
  display: flex;
  justify-content: space-between;
  text-decoration: none;
  padding-bottom: 1rem;
}



/* CSS Styling for Article.js */

.card {
    width: 70%;
    margin: 1rem;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 2.5px 7px 10px rgba(0, 0, 0, 0.393);
}

.video-pill {
    border-radius: 1.5rem;
    line-height: 1.5rem;
    font-size: 1rem;
    color: white;
    background-color: #e36414;
    padding: 0.25rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    box-sizing: content-box;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 1.5rem;
    text-decoration: none;  
}

.video-pill:hover {
    cursor: pointer;
}

.article-header {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
}
